* {
	box-sizing: border-box;
	font-family: "Dosis", sans-serif;
	scroll-behavior: smooth;
	margin: 0;
	padding: 0;
	overscroll-behavior: none;
}

#navbar, .contact-page, #welcome-section, #projects, .contactCloseButton, #contactOption  {
	background-repeat: repeat;
	background-size: auto;
}

#navbar {
	position: fixed;
	z-index: 3;
	width: 100vw;
	height: 10vh;
	border-bottom: 3px solid #235789;
	background-image: url('Items/Background/image-dhtx9bv8.png');
}

ul {
	width: 40vw;
	height: 100%;
	margin: auto;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	font-size: 1.5rem;
	font-weight: 550;
	list-style-type: none;
}

.contact-page {
	width: 100vw;
	height: 90vh;
	z-index: 4;
	position: fixed;
	left: 0;
	bottom: 0;
	background-image: url('Items/Background/image-9jgxcl07.png');
	scroll-behavior: unset;
}

#navbar ul {
	float: left;
}

#navbar a:hover, #contact a:hover {
	color: #9B9BA6;
}

#contact ul {
	float: right;
}

a {
	text-decoration: none;
	font-size: 1.3rem;
	color: #FBF5F3;
}

.contact {
	position: relative;
	z-index: 4;
}

#welcome-section {
	height: 100vh;
	width: 100vw;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	z-index: 2;
	background-image: url('Items/Background/image-hvatpscs.png');
}

#projects {
	padding: 50px;
	width: 100vw;
	display: grid;
	grid-auto-flow: column;
	grid-template-rows: repeat(2, 1fr);
	justify-content: space-around;
	gap: 20px;
	background-color: #9B9BA6;
	border-bottom: 3px solid #235789;
	border-top: 3px solid #235789;
	position: relative;
	z-index: 2;
	background-image: url('Items/Background/image-e6xbx7gl.png');
}

.last-bar {
	width: 100%;
	height: 50px;
	background-image: url('Items/Background/image-9jgxcl07.png');
	position: relative;
	z-index: 2;
}

.project-tile {
	background-color: #2F4C71;
	padding: 3px;
	border-radius: 10px;
	position: relative;
	height: 95%;
	will-change: transform;
}

.image {
	position: relative;
	border-radius: 7px;
	object-fit: cover;
	height: 100%;
	width: 100%;
	overflow: hidden;
}

.window {
	width: 100%;
	height: 100%;
	position: absolute;
	border: 3px solid #5F7998;
	background-color: #2F4C7199;
	border-radius: 10px;
	left: 0;
	top: 0;
	z-index: 1;
	overflow: hidden;
}

h1 {
	font-size: 5rem;
}

h3 {
	font-weight: 700;
}

#status {
	font-size: 2.5rem;
	color: #235789;
}

img {
	width: 100%;
}

.caption {
	color: #FBF5F3;
	font-size: 1.5rem;
	width: 100%;
	height: 35px;
	text-align: center;
	background-color: #5F7998;
	position: absolute;
	border-radius: 0px 0px 5px 5px;
	bottom: 0;
}

.project-tile:hover {
	.caption {
		background-color: #A49394;
	}
}

.contactCloseButton {
	height: 45px;
	width: 45px;
	border-radius: 50%;
	position: fixed;
	top: 100px;
	left: 20px;
	border: none;
	background-color: #4d3135;
	color: #FBF5F3;
	font-size: 25px;
	border: 2px solid #235789;
	background-image: url('Items/Background/image-cn56pp3e.png');
}

.myImage {
	top: 20%;
	left: 35%;
	width: 350px;
	transform: translate(-50%, -50%);
	border-radius: 50%;
	border: 2px solid #235789;
	position: fixed;
}

.contactOptions {
	display: flex;
	flex-direction: column;
	margin-left: 30px;
	margin-top: 60px;
	padding-bottom: 100px;
	justify-content: space-evenly;
	gap: 30px;
	height: 100%;
}

.contactOption {
	background-color: white;
	height: 90px;
	width: 30%;
	padding: 10px 30px 10px 30px;
	border-radius: 30px;
	align-items: center;
	border: 2px solid #235789;
	background-image: url('Items/Background/image-cn56pp3e.png');
	display: flex;
	flex-direction: column;
	gap: 10px;
	overflow: hidden;
}

p {
	color: #FBF5F3;
	font-size: 1.5rem;
}

#about-me-text {
	font-size: 1rem;
	width: 300px;
	text-align: left;
}

.contactLink {
	text-align: right;
}

#about-me {
	position: fixed;
	display: flex;
	flex-direction: column;
	align-items: center;
	top: 100px;
	right: 40px;
	z-index: 1;
	border: 2px solid #235789;
	color: black;
	text-align: center;
	padding: 15px;
	gap: 15px;
	overflow: hidden;
}


.welcome-container {
	background-image: url('Items/Background/image-hvatpscs.png');
}

@media (max-width: 1200px) {
	#projects {
		grid-template-rows: repeat(3, 1fr);
	}

	.project-tile {
		width: 40vw;
		height: calc(12vw * 2);
	}
}

@media (max-width: 800px) {
	#projects {
		grid-template-rows: repeat(6, 1fr);
	}

	.project-tile {
		width: 80vw;
		height: calc(22vw * 2);
	}

	ul {
		width: 60vw;
	}

	.contactLink {
		text-align: left;
		font-size: 1.1rem;
	}

	.contactOptions {
		height: 100%;
		margin-top: 15%;
	}

	.contactOption {
		height: 80px;
		width: 90vw;
	}

	#about-me {
		display: none;
	}

	.myImage {
		display: none;
	}

	h1 {
		font-size: 4rem;
	}
}

@media (max-width: 500px) {
	ul {
		width: 100vw;
	}
}